export default {
    name: "offDuty",
    data(){
        return{
            scroll: null
        }
    },
    methods:{
        offDuty(){
            const _this = this;
            this.$vux.confirm.show({
                title: '提示',
                content: '确定退出？',
                onConfirm () {
                    _this.common.commonFun.ajax({
                        dom: _this,
                        url: _this.common.apiUrl.LoginOut,
                        type: "post",
                        data: {
                            user_id: window.localStorage.getItem("user_id"),
                            getui_id:window.localStorage.getItem("Cid"),
                            client_id:window.localStorage.getItem("clientId"),

                        },
                        success: (response, allResponse) => {
                            // 显示
                            _this.$vux.loading.show({
                                text: '加载中'
                            });
